.page-header {
  position: relative;
  padding: 24px 40px 0 32px;
  margin-bottom: 126px;
  display: flex;

  @media (max-width: $mobile-width) {
    padding: 20px 16px;
    margin-bottom: 361px;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 768px;
    background: url("../img/mainBg.jpg") no-repeat center 0;
    z-index: -1;

    @media (max-width: $mobile-width) {
      background: url("../img/mainBg-mobile.jpg") no-repeat center 0;
      min-height: 640px;
    }
  }
}

.page-header__logo {
  width: 134px;
  height: 52px;
  background: url("../img/logo-group.svg") no-repeat;

  a {
    display: block;
    width: 100%;
    height: 100%;
  }

  @media (max-width: $mobile-width) {
    width: 96px;
    height: 36px;
    flex: 0 0 50%;
  }
}

.page-header__toggle {
  display: none;
  font-size: 14px;
  line-height: 16px;
  color: #f0f0f0;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-feature-settings: "pnum" on, "lnum" on;

  @media (max-width: $tablet-width) {
    display: block;
    flex: 0 0 50%;
    text-align: right;
  }

  a {
    display: block;
    padding-right: 30px;
    position: relative;
    color: inherit;
    text-decoration: none;

    &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 14px;
      background: url("../img/burger.svg") no-repeat;
      top: 0;
      right: 0;
    }
  }
}
